<template>
  <div>
    <el-row :gutter="20" type="flex" justify="space-between" align="center">
      <el-col :span="9">
        <div class="Head">
          <h2></h2>
          <el-select v-model="tutorType" placeholder="" style="margin-bottom:10px">
            <el-option label="regular" value="regular">
            </el-option>
            <el-option label="graduate" value="graduate">
            </el-option>
          </el-select>
          <hr>
        </div>
      </el-col>

      <el-col :span="15">
        <el-row :gutter="20" type="flex" justify="center" align="center">
          <el-date-picker v-model="pubDate" type="daterange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
          <el-button type="" @click="changeDate" style="margin-left:10px"></el-button>
          <el-button type="" @click="init" style="margin-left:10px"></el-button>
        </el-row>
      </el-col>
    </el-row>
    <el-alert v-if="this.$store.state.admin.currentBatch[this.tutorType]===5" title="" type="success" :closable="false">
    </el-alert>
    <el-table :data="stuList" :row-class-name="tableRowClassName">
      <el-table-column prop="stuName" label="">
      </el-table-column>
      <el-table-column prop="stuNum" label="">
      </el-table-column>
      <el-table-column prop="stuClass" label="">
      </el-table-column>
      <el-table-column prop="stuTelephone" label="">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="id" label="">
      </el-table-column>
    </el-table>
    <el-button type="" @click="endPub" v-if="endPubFlag"></el-button>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tutorType: 'regular',
      stuList: [],
      pubDate: [],
      batch: 5
    }
  },
  methods: {
    endPub () {
      this.axios.post('/admin/setbatch', { grade: this.$store.state.admin.currentGrade, type: this.tutorType, batch: 5 }).then(res => {
        console.log(res)
        this.init()
        this.$store.commit('FlashBatch')
        if (res.data.success) {
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'error',
            message: res.data.err
          })
        }
      }).catch(err => {
        this.$message({
          type: 'warning',
          message: err
        })
      })
    },
    tableRowClassName ({ row, rowIndex }) {
      if (!row.name) {
        return 'warning-row'
      }
      return ''
    },
    init () {
      this.axios.post('/admin/querypub', { grade: this.$store.state.admin.currentGrade, type: this.tutorType }).then(res => {
        this.pubDate = []
        this.pubDate.push(new Date(res.data.start))
        this.pubDate.push(new Date(res.data.end))
      })

      this.axios.post('/admin/final', { grade: this.$store.state.admin.currentGrade, type: this.tutorType }).then(res => {
        this.stuList = res.data.stuList
      })
    },
    changeDate () {
      console.log(this.pubDate)
      this.axios.post('/admin/setpub', { grade: this.$store.state.admin.currentGrade,
        type: this.tutorType,
        start: this.pubDate[0],
        end: this.pubDate[1] }).then(res => {
        this.init()
        if (res.data.success) {
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'error',
            message: ':' + res.data.err
          })
        }
      }).catch(err => {
        this.$message({
          type: 'warning',
          message: err
        })
      })
    }
  },
  created () {
    this.init()
  },
  watch: {
    tutorType: {
      handler: function (val, oldval) {
        this.init()
      },
      deep: true// 
    }
  },
  computed: {
    endPubFlag () {
      if (this.$store.state.admin.currentBatch[this.tutorType] === 4) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .Head {
    hr {
      width: 100%;
    }
    margin-bottom: 10px;
  }
</style>
